import React from "react";
import { useTranslation, Trans } from "react-i18next";

export const LandingBanner = (props) => {
    const { t } = useTranslation("translation");
    return (
        <section className="banner-section col-12">
            <div className="banner">
                <h1>
                    <Trans t={t}>VietCourses cung cấp các khóa học online đa lĩnh vực</Trans>
                </h1>
                <h2>
                    <Trans t={t}>
                        Chỉ cần vài bước đăng ký tài khoản, bạn có thể học mọi lúc, mọi nơi cùng các giảng viên hàng đầu
                        đến từ các trường đại học trong nước
                    </Trans>.
                </h2>
                <button id="signup2">
                    <Trans t={t}>Đăng ký ngay</Trans>
                </button>
            </div>
            <div className="backgr"></div>
            <div className="backgrImg"></div>
        </section>
    );
};
export default LandingBanner;
